.App {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
}
@font-face {
  font-family: 'iconfont';  /* project id 1318507 */
  src: url('//at.alicdn.com/t/font_1318507_l3t68y29y6j.eot');
  src: url('//at.alicdn.com/t/font_1318507_l3t68y29y6j.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_1318507_l3t68y29y6j.woff2') format('woff2'),
  url('//at.alicdn.com/t/font_1318507_l3t68y29y6j.woff') format('woff'),
  url('//at.alicdn.com/t/font_1318507_l3t68y29y6j.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_1318507_l3t68y29y6j.svg#iconfont') format('svg');
}
.iconfont{
  font-family:"iconfont" !important;
}
li{
  list-style: none
}
main{
  flex:1;
  overflow: hidden;
}
.App-footer{
  width:100%;
  height: 60px;
  position: fixed;
  bottom:0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #7B68EE;
  z-index: 100;
  
}
.App-footer .active{
  color: #ffffff;
}
a{
  text-decoration: none;
}
.swipers img{
  height:100%;
}
.home{
  height:100%;
}
.ab1{
  width:100%;
}
.ab1 img{

  width: 25%;
}
.ab1 li{
  width:100%;
  position: relative;
}
.ab1 li .ab2{
  width:40%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  line-height:20px;
  position: absolute;
  left:0;
  top:0;
  bottom:0;
  right:0;
  margin:auto;
}
.ab2 button{
  width:70px;
  color:red;
}
.active{
  color:red;
}

.list ul li{
  border-bottom:1px solid #7B68EE;
  height:70px;
  position: relative;
}
.list ul li div{
  height:30px;
  position: absolute;
  bottom: 0;
  left:0;
  right:0;
  top:0;
  margin: auto;
}
.list ul li div a{
  display: block;
}
.list .yy{
  text-align: center;
}
.list .yy span{
  font-size: 25px;
  color:#7B68EE;
}
.my{
  background-color: darkgrey;
}
.my-top{
  height:120px;
  background-color: darkmagenta;
}
.my-center{
  height:80px;
  position: relative;
}
.my-bottom{
  margin-top:80px;
  overflow: hidden;
}
.ab .txt{
  text-align: center;
  margin:10px auto;
}
.ab .txt a{
  font-size: 20px;
  display: inline-block;
  margin-left: 10px;
}
.prefixCls{
  width:70px;
}
.ac li img{
  width:25%;
}
.detail img{
  width:100%;
}
.detail .center div{
  background: darkslategray;
  width:100%;
  height:60px;
  color: #ffffff;
  font-size: 26px;
  text-align: center;
  line-height: 50px;
  position: absolute;
  bottom:0px;
  z-index: 100;
}
.my-top{
  position: relative;
}
.top1{
  width:50%;
  height:50%;
  position: absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  margin:auto;
  text-align: center;
  color:#ffffff;
}
.top1 i{
  font-size: 50px !important;
}
.top1 a{
  font-size: 20px;
}
.wrap{
  width:60%;
  height:50%;
  position: absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  margin:auto;
  text-align: center;
}
.cen1{
  display: inline;
  margin-right: 20px;
  color:green;
}
.cen2{
  display: inline;
  color: red;
}
span i{
  font-size: 25px !important;
}
.sale{
  width:100%;
}
.sale img{
  width:100%;
}
.cart-header{
  text-align: center;
  background-color: #FFD700;
}
.space-carousel {
  padding: 16px;
  background: #DEF1E5;
  overflow: hidden;
}
.imgs{
  height:70px;
}
.cart-center img{
  height:100px;
  width:100px;
  margin-top:20px;
}
.cart-footer li{
  margin-top:20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.cart-footer li div{
  width:25%;
  text-align: left;
}
.cart-footer li:nth-child(odd){
  background-color:#FF82AB;
}
.cart-footer li:nth-child(even){
  background-color: #FFA07A;
}
.ab1 p{
  font-size: 12px;
}
.ab1 span{
  width:100%;
  font-size: 12px;
}
